<template>
    <canvas id="countTimer"
        :width="width"
        :height="height"
        :style="styleObj"></canvas>
</template>

<script>
export default {
  name: "CountDown",
  props: {
    height: {
      type: Number,
      required: false,
      default: 150
    },
    width: {
      type: Number,
      required: false,
      default: 300
    },
    deadline: {
      type: String,
      required: true
    },
    gap: {
      type: Number,
      required: false,
      default: 5
    },
    padding: {
      type: Number,
      required: false,
      default: 20
    }
  },
  data() {
    return {
      perWidth: 0,
      perHeight: 0,

      rectStartX: 0,
      rectStartY: 0,
      styleObj: {}
    };
  },
  mounted() {
    this.styleObj = {
      width: this.width + "px",
      height: this.height + "px"
    };
    window.requestAnimationFrame(this.drawTime);
  },

  methods: {
    drawTime() {
      let ctx = document.getElementById("countTimer").getContext("2d");
      ctx.translate(0, 0);
      // clear canvas
      ctx.clearRect(0, 0, 300, 300);
      this.rectStartX = 0;
      this.rectStartY = 0;

      // ctx.save();
      let now = new Date();
      let balance = new Date(this.deadline).getTime() - now.getTime();

      let countDays = Math.floor(balance / 1000 / 60 / 60 / 24); //剩余天
      let countHours = Math.floor((balance - countDays * 24 * 60 * 60 * 1000) / 1000 / 60 / 60); //剩余天
      let countMinutes = Math.floor((balance - countDays * 24 * 60 * 60 * 1000 - countHours * 1000 * 60 * 60) / 1000 / 60); //剩余天
      let countSeconds = Math.floor((balance - countDays * 24 * 60 * 60 * 1000 - countHours * 1000 * 60 * 60 - countMinutes * 1000 * 60) / 1000); //剩余天

      this.perWidth = (this.width - 3 * this.gap - 2 * this.padding) / 4;
      this.perHeight = this.height;

      ctx.save();
      ctx.translate(this.padding, 0);
      if (countDays >= 0) {
        this.drawCard(ctx, countDays, "天");
      }

      if (countHours >= 0) {
        this.drawCard(ctx, countHours, "小时");
      }

      if (countMinutes >= 0) {
        this.drawCard(ctx, countMinutes, "分钟");
      }
      if (countSeconds >= 0) {
        this.drawCard(ctx, countSeconds, "秒");
      }
      ctx.restore();
      window.requestAnimationFrame(this.drawTime);
    },

    drawCard(ctx, value, type) {
      ctx.save();
      ctx.translate(this.rectStartX, this.rectStartY);
      ctx.fillRect(0, 0, this.perWidth, this.perHeight);

      ctx.save();
      ctx.font = "bold  40px Sans-serif ";
      ctx.textAlign = "center";
      ctx.textBaseline = "middle";
      ctx.fillStyle = "rgb(255,255,255)";
      ctx.fillText(value, this.perWidth / 2, this.height / 2);
      ctx.restore();

      ctx.save();
      ctx.font = "15px Sans-serif ";
      ctx.textAlign = "center";
      ctx.textBaseline = "bottom";
      ctx.fillStyle = "rgb(193, 213, 255)";
      ctx.fillText(type, this.perWidth / 2, this.height - 10);
      ctx.restore();

      this.rectStartX += this.perWidth + this.gap;
      this.rectStartY += 0;
      ctx.restore();
    }
  }
};
</script>

<style scoped lang="scss">
canvas {
  // width: 300px;
  // height: 100px;
}
</style>
