import axios from "axios";
import Vue from "vue";
import QS from "qs";
import router from "@/router";
import store from "@/store";

let expireTimer = null;
Vue.cancelTokenArr = []; //
const CancelToken = axios.CancelToken;

let paramsSerializer = function(params) {
  return QS.stringify(params, { arrayFormat: "indices" });
};

let instance = axios.create({
  baseURL: "/api",
  timeout: 100000,
  paramsSerializer: function(params) {
    return paramsSerializer(params);
  }
});

// instance.defaults.retry = 2;
// instance.defaults.retryDelay = 1000;
// instance.defaults.headers["Cache-Control"] = "max-age=86400";

instance.interceptors.request.use(
  config => {
    config.cancelToken = new CancelToken(cancel => {
      Vue.cancelTokenArr.push(cancel);
    });
    return config;
  }
  // error => {
  //   console.log("interceptors-error", error);
  //   return Promise.reject(error);
  // }
);

//响应拦截器
instance.interceptors.response.use(res => {
  let result = res.data;
  let statusCode = res.status;
  let statusText = res.statusText;
  return new Promise((resolved, reject) => {
    if (statusCode >= 200 && statusCode < 300) {
      if (Object.prototype.toString.call(result) === "[object Object]") {
        if (result.hasOwnProperty("ret")) {
          if (result.ret == -1) {
          } else if (result.ret == 1) {
            //1 为接口出错
            if (typeof result.msg != "string") {
              reject(new Error(JSON.stringify(result.msg)));
            } else {
              reject(new Error(result.msg));
            }
          } else {
            resolved(result);
          }
        } else {
          reject(new Error("The response has not 'ret'!"));
        }
      } else {
        //返回的格式不是object类型
        reject(new Error("The format of the response is incorrect!"));
      }
    } else if (statusCode > 400 || statusCode < 600) {
      reject(new Error(statusText));
    }
  });
});

export default instance;
