<template>
    <div class="next-activity">
        <div class="activity-wrap">
            <div v-for="(activity,index) in values"
                :key="index"
                class="card">
                <div class="up">
                    <div class="btn"
                        @click="add_to_mobile">
                        <i class="iconfont fs-calender"></i>
                    </div>
                    <div class="date">
                        <div style="font-weight: lighter;">{{activity.date.day}}</div>
                        <div>
                            <i class="iconfont fs-clock"></i>
                            <span style="font-weight: bold;">{{activity.date.time}}</span>
                        </div>
                    </div>

                </div>
                <div class="down">
                    <div class="brand">
                        {{activity.brand}}
                    </div>
                    <div class="type">
                        {{activity.type}}
                    </div>
                </div>
            </div>
        </div>

    </div>
</template> 
   
<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import fetch from "@/services/fetch";

export default {
  name: "NextActivity",
  components: {},

  // mixins:[混入的组件],

  /* 在组件设置 v-model时修改默认value和input */

  model: {
    prop: "checked", //注意当自定义v-mode的prop后，在下面 props中还要显式声明 类型等
    event: "change"
  },

  /*父组件传入的属性的类型*/

  props: {
    values: {
      type: Array,
      required: true
    }
  },

  data() {
    return {};
  },

  computed: {
    // ...mapState("模块名称", ["state名称"]),
    // ...mapGetters("模块名称", ["getter名称"]),
    now() {
      return Date.now(); //不变
    }
  },

  /* 依赖注入， 本地通过this.injectName调用。 */

  // inject: ["injectName"],

  watch: {
    msg() {
      console.log("msg is changed");
    },
    values: {
      handler: function(val) {},
      deep: true
    }
  },

  created() {},
  mounted() {},

  methods: {
    // ...mapMutations("模块名称", ["mutation名称"]),
    // ...mapActions("模块名称", ["action名称"]),

    add_to_mobile() {
      window.open("data:text/calendar;charset=utf8," + escape(this.getCalenderFile()));
    },
    getCalenderFile() {
      const startDateTime = "20200403T170000";
      const endDateTime = "20200403T180000";
      const datetime = "Friday, 3rd April 2020, 5pm";
      const location = "XXX street";
      const icsMSG =
        "BEGIN:VCALENDAR\nPRODID:-//Microsoft Corporation//Outlook 16.0 MIMEDIR//EN\nVERSION:2.0\nMETHOD:REQUEST\nX-MS-OLK-FORCEINSPECTOROPEN:TRUE\nBEGIN:VEVENT\nDESCRIPTION:When: " +
        datetime +
        ";Where: " +
        location +
        '\nDTSTART;TZID="China/Beijing":' +
        startDateTime +
        '\nDTEND;TZID="China/Beijing":' +
        endDateTime +
        "\nLOCATION:" +
        location +
        "\nSUMMARY;LANGUAGE=en-sg:You have a appointment\nX-MICROSOFT-CDO-BUSYSTATUS:TENTATIVE\nX-MICROSOFT-CDO-IMPORTANCE:1\nX-MICROSOFT-CDO-INTENDEDSTATUS:BUSY\nX-MICROSOFT-DISALLOW-COUNTER:FALSE\nX-MS-OLK-CONFTYPE:0\nBEGIN:VALARM\nTRIGGER:-PT15M\nACTION:DISPLAY\nDESCRIPTION:Reminder\nEND:VALARM\nEND:VEVENT\nEND:VCALENDAR\n";
      return icsMSG;
    }
  }
};
</script> 
  
<style lang="scss" scoped>
.next-activity {
  padding-bottom: 30px;
  overflow-x: auto;

  .activity-wrap {
    display: flex;
    width: max-content;

    .card {
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      padding: 10px 20px;
      width: 150px;
      height: 200px;
      background-color: blue;
      box-shadow: -3px 3px #5151fc;
      color: white;
      margin-right: 10px;

      &:last-child {
        margin-right: 0;
      }

      .up {
        flex: 1;
        display: flex;
        flex-direction: column;
        border-bottom: 1px solid #0230ff;

        .btn {
          flex: 1;
          align-self: flex-end;
        }

        .date {
          flex: 1;
          font-size: 15px;
          line-height: 1.5;
        }
      }
      .down {
        flex: 1;
        display: flex;
        flex-direction: column;

        .brand {
          flex: 1;
          display: flex;
          align-items: center;
          font-weight: bold;
        }
        .type {
          font-weight: lighter;
        }
      }
    }
  }
}
</style> 